@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
.Modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.16);
}

.Modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 900px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
}
.Modal > * {
  margin-left: 16px;
  margin-right: 16px;
}
.Modal > *:first-child {
  margin-top: 16px;
}
.Modal h2.title {
  font-weight: 600;
  color: #333;
}
.Modal .content {
  border-top: 1px solid #bebebe;
  margin-top: 16px;
}
.Modal .content p {
  padding: 8px;
  font-size: 12pt;
  text-align: left;
  color: #999;
}
.Modal .button-wrap {
  margin: 0;
  margin-top: 8px;
}
.Modal .button-wrap button {
  width: 100%;
  padding: 12px 0;
  border-radius: 0 0 10px 10px;
  background-color: #3e3e3e;
  font-size: 13pt;
  color: #fff;
  border: 0;
  cursor: pointer;
}
.Modal .button-wrap button:hover {
  background-color: #bebebe;
  outline: none;
}
.Modal .button-wrap button:active {
  background-color: #bebebe;
  outline: none;
}

html,
body {
  font-family: "Poppins", "Noto Sans KR" sans-serif;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

header,
footer {
  user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}
body {
  width: 100%;
  min-height: 100%;
}

#root,
.App {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

/* Common Layout */

.container {
  width: 100%;
  height: auto;
  padding-top: 50px;
  margin: -50px 0 -40px 0;
  text-align: center;

  /* header & footer height */
}

/* text-title style */
.text-title {
  padding-top: 300px;
  font-size: 4rem;
}

.text-subtitle {
  font-size: 1.5rem;
  font-weight: 300;
}

.text-bold {
  color: rgb(23, 101, 80);
  font-weight: 400;
}

.text-content {
  font-size: 0.9rem;
  margin: 10px;
}

/* Home */
.home__container {
  width: 100%;
  min-height: 100%;
  position: relative;
  padding-top: 50px;
  margin: -50px 0 -40px 0;
  top: 0;
  left: 0;
  z-index: -10;
  /* footer height */
  background-image: url(/static/media/main.06f704a2.jpg);
  background-size: cover;
  background-attachment: fixed;
  text-align: center;
}

/* About page */
.about_title {
  padding-top: 5rem;
}
.profile {
  width: 180px;
  border-radius: 50%;
}

.icon {
  margin-right: 10px;
}

/* Projects page */
.project__wrapper {
  padding-top: 250px;
}
.project__list {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.project__img {
  width: auto;
  height: 250px;
  border-radius: 10px;
  box-shadow: 8px 8px 10px rgba(0, 0, 0, 0.2);
  margin: 10px;
}
.project__img:hover {
  height: 260px;
  transition: 0.3s;
}

